<script>
import Swal from "sweetalert2";
export default {
  name: "Home",
  components: {},
  data: () => ({
    array_device: [],
    Devicename: "Device Name:",
    Deviceid: "ID:",
    loading: false,
    user_login: "",
    check_data: "",
    bluetoothDevice: null,
    statusCharacteristic: null,
    server: null,
    service: null,
    characteristic: null,
    dialog_Loading: true,
    otp_admin: "123454",
    name: "",
    id: "",
    status: "connect",
    status_icon: "mdi-bluetooth-off",
    status_icon_btn: "mdi-arrow-right-circle",
    status_lock: "lock",
    statuslock_icon: "mdi-lock-outline",
    devicegatt: null,
    backgroung_state_device:
      "background: linear-gradient(to right, #e52d27, #b31217);",
    checkserver: "",
  }),
  mounted: {},
  computed: {
    date_name_device() {
      var name = this.$store.state.name_device.name;
      return name;
    },
    date_id_device() {
      var id = this.$store.state.name_device.id;
      return id;
    },
  },
  created() {
    this.get_user_login();
    this.ScanDevice();
    this.clean_state();
  },
  methods: {
    ScanDevice() {
      return (this.bluetoothDevice
        ? Promise.resolve()
        : this.requestDevice()
      ).catch((error) => {
        console.log("Argh! " + error);

        Swal.fire({
          icon: "error",
          text: "Please scan again!",
        });
        //  this.ScanDevice();
      });
    },
    clean_state() {
      var state = "";
      this.$store.commit("check_state", state);
      // this.$store.state.name_device.gatt.disconnect();
    },
    requestDevice() {
      if (this.user_login === "onedooraccess") {
        console.log("Requesting any Bluetooth Device...");
        console.log("onedooraccess---->");
        return navigator.bluetooth
          .requestDevice({
            acceptAllDevices: true,
            optionalServices: ["1fb32be3-958f-463c-893e-f9e33911f256"],
          })
          .then((device) => {
            var state = "show";
            this.bluetoothDevice = device;

            this.$store.commit("data_device", device);
            this.$store.commit("check_state", state);
            // console.log(state)
          });
      } else if (this.user_login === "oneboxaccess") {
        console.log("Requesting any Bluetooth Device...");
        console.log("oneboxaccess---->");
        return navigator.bluetooth
          .requestDevice({
            acceptAllDevices: true,
            optionalServices: [0xff00],
          })
          .then((device) => {
            var state = "show";
            this.bluetoothDevice = device;

            this.$store.commit("data_device", device);
            this.$store.commit("check_state", state);
            // console.log(state)
          });
      } else {
        console.log("no user_login");
      }
    },
    connectDeviceAndCacheCharacteristics_doorlock() {
      if (this.status_lock == "lock") {
        this.status_lock = "unlock";
        this.statuslock_icon = "mdi-lock-open-outline";

        if (this.bluetoothDevice.gatt.connected && this.statusCharacteristic) {
          return Promise.resolve();
        }

        return this.bluetoothDevice.gatt
          .connect()
          .then((server) => {
            console.log("server--->");
            console.log(
              server.getPrimaryService("1fb32be3-958f-463c-893e-f9e33911f256")
                .Promise
            );
            if (
              server.getPrimaryService("1fb32be3-958f-463c-893e-f9e33911f256")
                .Promise != null
            ) {
              this.checkserver = "connectserver";
              return server.getPrimaryService(
                "1fb32be3-958f-463c-893e-f9e33911f256"
              );
            } else {
              Swal.fire({
                icon: "error",
                text: "Server is null or empty",
              });
            }
          })
          .then((service) => {
            console.log("service--->");
            console.log(this.checkserver);
            return service.getCharacteristic(
              "da467c88-9e6a-4d73-8f15-1dca6bc67419"
            );
          })
          .then((characteristic) => {
            console.log("characteristicr--->");
            const encoder = new TextEncoder("utf-8");
            const userDescription = encoder.encode(this.otp_admin);
            characteristic.writeValue(userDescription);

            if (this.checkserver === "connectserver") {
              this.$message
                .loading("Unlock Device...", 1.5)
                .then(() => this.$message.success("Unlock success", 1.5));

              this.Timeout_StatusLock_unlock();
            }
          });
      }
    },
    connectDeviceAndCacheCharacteristics_boxlock() {
      console.log("connectDeviceAndCacheCharacteristics_boxlock");
      if (this.status_lock == "lock") {
        this.status_lock = "unlock";
        this.statuslock_icon = "mdi-lock-open-outline";

        if (this.bluetoothDevice.gatt.connected && this.statusCharacteristic) {
          return Promise.resolve();
        }

        return this.bluetoothDevice.gatt
          .connect()
          .then((server) => {
            console.log("server--->");
            console.log(server);

            if (server.getPrimaryService(0xff00) != null) {
              this.checkserver = "connectserver";
              return server.getPrimaryService(0xff00);
            } else {
              Swal.fire({
                icon: "error",
                text: "Server is null or empty",
              });
            }
          })
          .then((service) => {
            console.log("service--->");
            return service.getCharacteristic(0xff01);
          })
          .then((characteristic) => {
            console.log("characteristicr--->");
            const encoder = new TextEncoder("utf-8");
            characteristic.writeValue(
              new Uint8Array([
                0x00,
                0x06,
                0xcc,
                0x59,
                0x51,
                0x3c,
                0x4c,
                0xaa,
                0x61,
                0x16,
                0xd3,
                0x4b,
                0xf7,
                0x10,
                0x00,
                0xb1,
                0x2e,
                0xf8,
              ])
            );
            console.log("------->", this.checkserver);
            if (this.checkserver === "connectserver") {
              this.$message
                .loading("Unlock Device...", 1.5)
                .then(() => this.$message.success("Unlock success", 1.5));

              this.Timeout_StatusLock_unlock();
            }
          });
      }
    },
    Status_connect_dis_Device() {
      this.bluetoothDevice = this.$store.state.name_device;
      // console.log(this.bluetoothDevice);
      if (this.$store.state.status == "connect") {
        // console.log(this.bluetoothDevice);
        if (this.bluetoothDevice != null) {
          this.bluetoothDevice.gatt.connect();
          this.devicegatt = this.bluetoothDevice.gatt.connect();

          this.$message
            .loading("Connect to Device...", 1.5)
            .then(() => this.$message.success("Connect Success", 2.0));

          this.status_icon_btn = "mdi-close-circle";

          var status = "disconnect";
          this.$store.commit("status", status);

          this.status_icon = "mdi-bluetooth-connect";
          this.loading = false;
        } else {
          Swal.fire({
            icon: "error",
            title: "Someting error occured !!",
            text: "Please Scan and Pair again",
          });
        }
      } else if (this.$store.state.status == "disconnect") {
        this.bluetoothDevice.gatt.disconnect();

        this.$message
          .loading("Disconnect to Device...", 1.5)
          .then(() => this.$message.success("Disconnect Success", 2.0));

        this.status_icon_btn = "mdi-arrow-right-circle";
        var status = "connect";
        this.$store.commit("status", status);
        this.status_icon = "mdi-bluetooth-off";

        this.loading = false;
      }
    },
    Timeout_StatusLock_unlock() {
      console.log(this.checkserver);
      if (this.checkserver === "connectserver") {
        setTimeout(
          () => (
            (this.status_lock = "lock"),
            (this.statuslock_icon = "mdi-lock-outline"),
            this.$message
              .loading("Action Lock..", 1.5)
              .then(() => this.$message.error("Lock success", 2.5))
          ),
          8000
        );
      } else {
        console.log("else");
      }
    },
    get_user_login() {
      let data = JSON.parse(
        window.localStorage.getItem("user_login_oneaccess")
      );
      // console.log(data)
      if (data === null) {
        this.user_login = "";
      } else {
        this.user_login = data;
      }
    },
  },
};
</script>
